
  //引入邀请卡生成相关
  var lrz = require("lrz");
  var $ = require("jquery");
  window.jQuery = $;

  //引入图片上传相关
  var CosCloud = require("../../../static/lib/js/cos-js-sdk-v4");
  var myFolderName = function () {
    var now = new Date();
    var nowYear = now.getFullYear();
    var nowMonth = now.getMonth() + 1;
    if(nowMonth < 10) {
      nowMonth = '0' + nowMonth;
    } else {
      nowMonth = String(nowMonth);
    }
    return '/image/' + nowYear + '-' + nowMonth + '/'
//    return '/image/' + nowYear + '-' + "05" + '/'
  };

  import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
;

  export default {
    props: ['imageData'], //数据格式: {"url": "xxxxx", "id": 123}
    data() {
      return {
        data: {
          noticeText: '',
          imgList: [],
          tempList: [] //临时储存图片数据的数组
        },
        uploadState: {
          uploadSuccess: false,
          uploadFalse: false,
          uploading: false,
          uploadingTxt: null,
          isOverflow: false,
          overflowTxt: 1
        },
        cosData: {
          bucket: 'webstatic',
          appid: '1252911372',
          sid: '',
          skey: '',
          region: 'gz',
          myFolder: myFolderName(),
          fileName: function () {
            var guid = "";
            for (var i = 1; i <= 32; i++) {
              var n = Math.floor(Math.random() * 16.0).toString(16);
              guid += n;
              if ((i == 8) || (i == 12) || (i == 16) || (i == 20))
                guid += "-";
            }
            return guid;
          },
          periodSign: '',
          onceSign: '',
          cosObject: null,
        },
        fileTargetName: ''  //input的Class
      }
    },
    mounted(){
      this.fileTargetName = "fileTarget" + (Math.random() * 10000).toFixed(0) + new Date().getTime(); //随机新建一个class给input元素,用于选择器选择
      this.imageData.isMultiple ? this.uploadState.overflowTxt = 6 : this.uploadState.overflowTxt = 1;
      console.log(this.imageData);
    },
    methods: {
      successCallBack: function (result) {
        console.log("==========上传到腾讯云成功,取回img url==========");
        console.log(result);

        this.uploadState.uploadingTxt = false;
        this.uploadState.uploadingTxt = 0;

        this.data.tempList = [];


        var image = {
          url: result.data.access_url.replace(/^http:\/\/[\w\d\-\_.]+\.com/i, ""),
          width: 0,
          height: 0,
          offsetX: 0
        };

        var self = this;
        //把图片信息传到服务器
        self.H.toAjax(true).then(function (data) { //data 公用参数
          var submitData = data;
          submitData.data.image = image;
          self.A.post("/v1/common/uploadImage", submitData).then(function (result) {

            console.log("===上传到服务成功,取回imgid,组装url放回vue.imageData.image===");
            self.imageData.image.push({id: result.data.data.imageId, url: image.url});
            self.uploadState.uploadSuccess = true;

          });
        });
      },
      errorCallBack: function (result) {
        console.log("==========上传到腾讯云失败==========");
        this.uploadState.uploadFalse = true;
        if (this.data.imgList.length > 0) {
          this.data.imgList.pop();
        }
      },
      progressCallBack: function (curr) {
        console.log('上传到腾讯云百分比:' + curr * 100 + "%");
        this.uploadState.uploading = true;
        this.uploadState.uploadingTxt = Math.floor(curr * 100) + "%";
        if (curr === 1) {
          this.uploadState.uploading = false;
        }
      },
      //点击input[file]
      addFile(e){

        console.info(e.target.files);
        console.info(document.querySelector("#file").value);
        //没选择文件是退出
        if (e.target.files.length > 6) {
//          document.querySelector("." + self.fileTargetName).value = "";
          this.uploadState.isOverflow = true;
          return;
        }
        if (e.target.files.length + this.data.imgList.length > 6) {
//          document.querySelector("." + self.fileTargetName).value = ""; //mark
          this.uploadState.isOverflow = true;
          return
        }
        if (document.querySelector("." + this.fileTargetName).value == "" || document.querySelector("." + this.fileTargetName).value.length == 0) {
          return;
        }

        //如果是多图上传
//        if(this.imageData.isMultiple)


        var self = this;
        self.uploadState.uploading = true;
        self.uploadState.uploadingTxt = "上传中";
        //先压缩图片
        var promise = function () {
          return new Promise(function (resolve, reject) {
            var allFilesNum = e.target.files.length;
            var len = allFilesNum ;
            dplrz();
            function dplrz(){
                --len;
                lrz(e.target.files[len])
                  .then(function (rst) {
                    // 处理成功会执行
                    console.log(rst);
                    console.log(len);
                    var image = new Image();
                    image.src = rst.base64; //返回 base64 码
                    image.onload = function () { // this => imgObject
                      self.data.tempList.push(rst);
                      self.data.imgList.push(rst);
                      console.log("格式", rst);
                      console.log("压缩后", rst.fileLen);
                      console.log("压缩后格式", rst.file.type);
                      if(len>0){
                        dplrz();
                      }else if(len==0){
                        document.querySelector("." + self.fileTargetName).value = ""; //压缩完成后删除input value
                        resolve();
                      }
                    }
                  })
                  .catch(function (err) {
                    // 处理失败会执行
                    console.log(err);
                    reject(false);
                  })
                  .always(function () {
                    // 不管是成功失败，都会执行
                    self.uploadState.uploading = false;
                  });
            }
          });
        };
        //压缩完成后获取cos签名 + 上传到腾讯云
        promise().then(function (successData) {
          console.log("=====压缩完成=====: " + successData);
          self.getCos();
          self.uploadImageToCos();
        }, function (falseData) {

        });
      },
      //获取cos签名与cos对象
      getCos(){
        if (this.cosData.cosObject == null) {
          console.warn("===获取cos签名与cos对象===");
          var _self = this;
          var cosObject = new CosCloud({
            appid: this.cosData.appid,// APPID 必填参数
            bucket: this.cosData.bucket,//bucketName 必填参数
            region: this.cosData.region,
            getAppSign: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 公用参数
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.periodSign;
                  callback(signCode);
                });
              });
            },
            getAppSignOnce: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 公用参数
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.onceSign;
                  callback(signCode);
                });
              });
            }
          });

          this.cosData.cosObject = cosObject;
        }
      },
      //上传到腾讯云
      uploadImageToCos(){
        var _self = this;
        console.warn("===上传文件到腾讯云===");

        for (var i in this.data.tempList) {
          var fileName = _self.cosData.fileName();
          // alert(fileName);
          this.cosData.cosObject.uploadFile(
            _self.successCallBack,
            _self.errorCallBack,
            _self.progressCallBack,
            _self.cosData.bucket,
            _self.cosData.myFolder + fileName + ".jpg",
            _self.data.tempList[i].file,
            1
          );
        }

      },
      //删除图片
      deleteImage(index){
        this.data.imgList.splice(index, 1);
        this.imageData.image.pop();
      },
      //检测当前已添加图片张数
      checkFileNums(e){
        //暂时阻止多张图片
        var isMultiple = this.imageData.isMultiple;
        if(isMultiple == null || isMultiple == undefined || isMultiple == ""){
          if(this.data.imgList.length  == 1) {
            this.uploadState.isOverflow = true;
            e.cancelable;
            e.stopPropagation();
            e.preventDefault();
            return false;
          }
        }
        if (this.data.imgList.length >= 6) {
          this.uploadState.isOverflow = true;
          e.cancelable;
          e.stopPropagation();
          e.preventDefault();
          return false;
        }
        if(this.uploadState.uploading){
          e.cancelable;
          e.stopPropagation();
          e.preventDefault();
          return false;
        }
      },
    },
    components: {
      icon: Icon,
      toast: Toast
    },
//    watch: {
//      'data.imgList'(){
////        alert(this.data.imgList.length);
//      }
//    }
  }



